import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import {  AppMin } from "../core";
import { Router, ActivatedRoute } from '@angular/router';


@Component({
  selector: 'app-mysetdata-info',
  templateUrl: './mysetdata-info.page.html',
  styleUrls: ['./mysetdata-info.page.scss'],
})

export class MysetdataInfoPage implements OnInit {
  header_class;
  jianjie;
  constructor(public navCtrl: NavController, public ActivatedRoute: ActivatedRoute, public Router: Router, public appmin?: AppMin) {

    if (this.appmin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class_md";
      console.log("判断为安卓系统 设置状态栏样式")
    }

  
    this.jianjie=this.ActivatedRoute.snapshot.queryParams['jianjie']//获取签名


  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MysetdataJianjiePage');
  }

  jianjieinfo(val) {
    this.jianjie = val; //获取输入的简介
  }
  baoc_click() {
    let obj = {
      type: "setUserData",
      userId: this.appmin.getUserid(),
      cname: '',
      sex: '',
      stratDate: '',
      qianming: this.jianjie

    }
    this.appmin.post(obj).subscribe((data) => {
      this.dataHandle(data);
    });

    this.navCtrl.pop();

  }
  dataHandle(meg: any) {

  }

  ngOnInit() {
  }

}
